<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">{{pageTitle}}</el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="right">
        <el-form-item label="资讯标题" prop="title">
          <el-input v-model="form.title" placeholder="请输入资讯标题" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="资讯描述" prop="desc">
          <el-input v-model="form.desc" placeholder="请输入资讯描述" type="textarea" autosize></el-input>
        </el-form-item>
        <el-form-item label="资讯图片" prop="image">
          <el-upload class="avatar-uploader"
                    action="http://szebra.cn/api/admin/images.json"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <img v-if="form.image" :src="form.image" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
            <i class="avatar-limit">图片小于1M,格式jpg,png,jpeg</i>
        </el-form-item>
        <el-form-item label="阅读基数" prop="base_number">
          <el-input-number v-model="form.base_number" label=""></el-input-number>
        </el-form-item>
         <el-form-item label="发布时间" prop="public_at">
            <el-date-picker
              v-model="form.public_at"
              align="right"
              type="date"
              placeholder="选择日期"
              :picker-options="pickerOptions1">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="资讯类别" prop="link_type">
          <el-radio-group v-model="form.link_type">
            <el-radio label="article">链接公众号文章</el-radio>
            <el-radio label="page">自定义页面</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.link_type == 'article'" label="文章地址" prop="link">
          <el-input v-model="form.link" placeholder="请输入公众号文章地址"></el-input>
        </el-form-item>
        <el-form-item v-else label="链接页面" prop="link">
          <el-select v-model="form.link" placeholder="请选择链接页面">
            <el-option v-for="pageItem in pageList" :key="pageItem.id" :label="pageItem.name" :value="pageItem.id">{{pageItem.name}}</el-option>
          </el-select>
        </el-form-item>
        <el-button type="primary" @click="submitForm('form')" style="">保存</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>
const G_IMAGE_SIZE = 2 // 图片大小

export default {
  components: {
  },

  data () {
    return {
      pageState: '', // 1新增，2编辑，3预览
      informationId: '', // 页面id
      pageTitle: '', // 页面标题
      form: {
        title: '',
        desc: '',
        position: 0,
        image: '',
        link_type: '',
        link: '',
        public_at: '',
        base_number: 0
      },
      rules: { // 校验规则
        title: { required: true, message: '请输入资讯标题名称', trigger: 'blur' },
        desc: { required: true, message: '请输入资讯描述', trigger: 'blur' },
        image: { required: true, message: '请上传资讯图片' },
        public_at: { required: true, message: '请输入发布时间', trigger: 'blur' },
        link: { required: true, message: '请输入链接', trigger: 'blur' }
      },
      pageList: [],
      pickerOptions1: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      }
    }
  },

  watch: {
    pageState (newValue) {
      switch (newValue) {
        case 1:
          this.pageTitle = '新增资讯'
          break
        case 2:
          this.pageTitle = '编辑资讯'
          break
        case 3:
          this.pageTitle = '资讯详情'
          break
      }
    }
  },

  methods: {
    // 查询资讯详情
    queryInfomationDetail () {
      let params = {
        information_id: this.informationId
      }
      this.$http.InfomationDetail(params, 'get').then(res => {
        let result = res.data
        let resultRows = result.result_rows || {}
        if (result.retcode === 0) {
          this.form = resultRows
        } else {
          this.$message.error(`${result.retcode}${result.message}`)
        }
      })
    },

    // 查询自定义页面列表
    queryPageList () {
      this.$http.pageList({}, 'get').then(res => {
        let result = res.data.result_rows
        this.pageList = result.pages
      })
    },

    // 提交表单
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = {
            information: this.form
          }
          if (this.pageState === 1) {
            this.$http.addInformation(params, 'post').then(res => {
              let result = res.data

              if (result.retcode === 0) {
                this.$message.success('新增成功')
                setTimeout(() => {
                  this.$router.push({
                    path: '/information/list'
                  })
                }, 300)
              } else {
                this.$message.error('新增失败')
              }
            })
          } else {
            params.information_id = this.informationId
            this.$http.editInformation(params, 'patch').then(res => {
              let result = res.data
              if (result.retcode === 0) {
                this.$message.success('修改成功')
                setTimeout(() => {
                  this.$router.push({
                    path: '/information/list'
                  })
                }, 300)
              } else {
                this.$message.error('修改失败')
              }
            })
          }
        } else {
          return false
        }
      })
    },

    handleAvatarSuccess (res, file) {
      let resultRows = res.result_rows || {}
      let retcode = res.retcode
      if (retcode === 0) {
        this.$message.success('上传成功')
        this.form.image = resultRows.url
        this.form.image_attributes = {}
        this.form.image_attributes.id = resultRows.id
      } else {
        this.$message.error(`${retcode}${res.message}`)
      }
      // this.form.imageUrl = URL.createObjectURL(file.raw)
    },

    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isJPEG = file.type === 'image/jpeg'
      const isPNG = file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < G_IMAGE_SIZE

      if (!isJPG && !isJPEG && !isPNG) {
        this.$message.error('上传头像图片只能是 JPG、jpeg、png格式!')
      }
      if (!isLt2M) {
        this.$message.error(`上传头像图片大小不能超过${G_IMAGE_SIZE} MB!`)
      }
      return isJPG && isLt2M
    }
  },

  mounted () {
    this.pageState = +this.$route.query.pageState || 1
    this.informationId = this.$route.query['information_id'] || ''
    if (this.pageState !== 1) {
      this.queryInfomationDetail()
    } else {
      // 新增页面默认选中微信公众号文章
      this.form.link_type = 'article'
    }
    this.queryPageList()
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .avatar-limit{
    font-size: 12px;
  }
</style>
